.header{
  min-width: 100%;
  cursor: pointer;
  position: fixed;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  background-color: #39445a;
  font-family: 'Montserrat', sans-serif;
  font-size: 4vw;
  padding-bottom: 10px;
  box-shadow: 0 1px 5px black;
  color: white;
  z-index: 100;
}


@media (max-width: 1000px) {
      .header {
        padding-top: 15px;
        font-size: 6.4vw;
      }
}


